:host {
  display: block;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

:host([vertical]) {
  flex-direction: row;
  align-items: flex-start;
}

:host(:not([vertical])) {
  width: var(--pfe-progress-steps__item--size, 75px);
  min-width: fit-content;
}

:host([vertical]:not(:last-child)) {
  min-height: var(--pfe-progress-steps__item--size, 75px);
}

:host([hidden]) {
  display: none;
}

:host([is_link]) {
  cursor: pointer;
}

:host([vertical]:not([has_description]):last-child) {
  margin-bottom: 1em;
}

.pfe-progress-steps-item__circle {
  box-sizing: border-box;
  position: relative;
  border-radius: 50%;
  border: var(--pfe-theme--ui--border-width--md, 2px) var(--pfe-theme--ui--border-style, solid) var(--pfe-theme--color--ui--border--lighter, #d2d2d2);
  width: var(--pfe-progress-steps-item__circle--size, var(--pfe-theme--ui--element--size, 20px));
  height: var(--pfe-progress-steps-item__circle--size, var(--pfe-theme--ui--element--size, 20px));
  z-index: 2;
  background-color: var(--pfe-progress-steps-item__circle--color, var(--pfe-theme--color--surface--lightest, #fff));
  margin: auto;
  margin: calc((var(--pfe-progress-steps-item__circle--size--active, var(--pfe-theme--ui--element--size--md, 32px)) - var(--pfe-progress-steps-item__circle--size, var(--pfe-theme--ui--element--size, 20px))) / 2) auto;
  transform: translateX(var(--pfe-theme--ui--border-width--md, 2px));
}

:host([variant="count"]) .pfe-progress-steps-item__circle::before {
  content: attr(count);
  text-align: center;
  line-height: calc(var(--pfe-progress-steps-item__circle--size--active, var(--pfe-theme--ui--element--size--md, 32px)) * .8);
  font-size: var(--pf-global--FontSize--xs, 0.75rem);
  font-weight: 600;
  position: absolute;
  top: -.2em;
  left: -.2em;
  width: 20px;
  height: 20px;
}

:host([state="active"]) .pfe-progress-steps-item__circle {
  border-color: var(--pfe-progress-steps-item__circle--color--active, var(--pfe-theme--color--ui-accent, #06c));
  background-color: var(--pfe-progress-steps-item__circle--color--active, var(--pfe-theme--color--ui-accent, #06c));
  color: var(--pfe-theme--color--text--on-saturated, #fff);
}

.pfe-progress-steps-item__circle::after {
  display: none;
  position: absolute;
  content: "";
  border-radius: 50%;
  transform: translate(-50%, -50%);
  color: var(--pfe-theme--color--text--muted, #6a6e73);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  width: var(--pfe-progress-steps-item__circle--size--active, var(--pfe-theme--ui--element--size--md, 32px));
  height: var(--pfe-progress-steps-item__circle--size--active, var(--pfe-theme--ui--element--size--md, 32px));
}

:host([state="active"]) .pfe-progress-steps-item__circle::after {
  display: block;
  background-color: var(--pfe-progress-steps-item__circle--color--active, var(--pfe-theme--color--ui-accent, #06c));
  opacity: var(--pfe-theme--opacity, 0.09);
}

.pfe-progress-steps-item__circle svg {
  z-index: 1;
  height: var(--pfe-progress-steps-item__circle--size, var(--pfe-theme--ui--element--size, 20px));
  width: var(--pfe-progress-steps-item__circle--size, var(--pfe-theme--ui--element--size, 20px));
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  vertical-align: -.125em;
}

:host([state="done"]) .pfe-progress-steps-item__circle svg {
  fill: var(--pfe-theme--color--feedback--success, #3e8635);
}

:host([state="error"]) .pfe-progress-steps-item__circle svg {
  fill: var(--pfe-theme--color--feedback--important, #c9190b);
}

.pfe-progress-steps-item__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: var(--pfe-progress-steps-item--spacer, var(--pfe-theme--content-spacer--body--sm, 0.5rem));
}

:host([vertical]) .pfe-progress-steps-item__content {
  align-items: flex-start;
  margin-top: calc((var(--pfe-progress-steps-item__circle--size--active, var(--pfe-theme--ui--element--size--md, 32px)) - var(--pfe-progress-steps-item__circle--size, var(--pfe-theme--ui--element--size, 20px))) / 2);
  margin-left: calc(((var(--pfe-progress-steps-item__circle--size--active, var(--pfe-theme--ui--element--size--md, 32px)) - var(--pfe-progress-steps-item__circle--size, var(--pfe-theme--ui--element--size, 20px))) / 2) + var(--pfe-progress-steps-item--spacer, var(--pfe-theme--content-spacer--body--sm, 0.5rem)));
}

.pfe-progress-steps-item__content--title,
.pfe-progress-steps-item__content--title slot::slotted(*) {
  font-size: var(--pf-global--FontSize--md, 1rem);
  color: var(--pfe-progress-steps-item__title--Color, var(--pfe-theme--color--text, #151515));
}

:host([is_link]) .pfe-progress-steps-item__content--title, :host([is_link])
.pfe-progress-steps-item__content--title slot::slotted(*) {
  color: inherit;
}

:host([is_link]:hover) .pfe-progress-steps-item__content--title, :host([is_link]:hover)
.pfe-progress-steps-item__content--title slot::slotted(*) {
  color: var(--pfe-progress-steps-item__title--Color, var(--pfe-broadcasted--link--hover, #004080));
  text-decoration: var(--pfe-broadcasted--link-decoration--hover, underline);
}

:host([is_link]:focus) .pfe-progress-steps-item__content--title, :host([is_link]:focus)
.pfe-progress-steps-item__content--title slot::slotted(*) {
  color: var(--pfe-progress-steps-item__title--Color, var(--pfe-broadcasted--link--focus, #004080));
  text-decoration: var(--pfe-broadcasted--link-decoration--focus, underline);
}

:host([is_link]:visited) .pfe-progress-steps-item__content--title, :host([is_link]:visited)
.pfe-progress-steps-item__content--title slot::slotted(*) {
  color: var(--pfe-progress-steps-item__title--Color, var(--pfe-broadcasted--link--visited, #6753ac));
  text-decoration: var(--pfe-broadcasted--link-decoration--visited, none);
}

:host([state="active"]) .pfe-progress-steps-item__content--title, :host([state="active"])
.pfe-progress-steps-item__content--title slot::slotted(*) {
  color: var(--pfe-progress-steps-item__title--Color--active, var(--pfe-theme--color--feedback--info, #06c));
  font-weight: var(--pfe-theme--font-weight--normal, 400);
}

:host([state="error"]) .pfe-progress-steps-item__content--title, :host([state="error"])
.pfe-progress-steps-item__content--title slot::slotted(*) {
  color: var(--pfe-progress-steps-item__title--Color--error, var(--pfe-theme--color--feedback--critical--lighter, #c9190b));
}

.pfe-progress-steps-item__content--description {
  color: var(--pfe-progress-steps-item__description--Color, var(--pfe-theme--color--text--muted, #6a6e73));
  font-size: var(--pf-global--FontSize--sm, 0.875rem);
  text-align: center;
}

:host([state="error"]) .pfe-progress-steps-item__content--description {
  color: var(--pfe-progress-steps-item__title--Color--error, var(--pfe-theme--color--feedback--critical--lighter, #c9190b));
}
